import React from "react";
import { SketchPicker } from "react-color";
import reactCSS from "reactcss";

class ColorPicker extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            displayColorPicker: false,
            color: this.props.value && this.props.value.rgb
                ? this.props.value.rgb : {
                    r: "241",
                    g: "112",
                    b: "19",
                    a: "1"
                }
        };
    }

    handleClick = () => {
        this.setState({ displayColorPicker: !this.state.displayColorPicker });
    };

    handleClose = () => {
        this.setState({ displayColorPicker: false });
    };

    handleChange = color => {
        this.setState({ color: color.rgb });
        this.props.onChange(color);
    };

    render() {
        const styles = reactCSS({
            default: {
                color: {
                    width: "36px",
                    height: "14px",
                    borderRadius: "2px",
                    background: `rgba(${this.state.color.r}, ${this.state.color.g}, ${this.state.color.b
                        }, ${this.state.color.a})`
                },
                swatch: {
                    padding: "5px",
                    background: "#fff",
                    borderRadius: "1px",
                    boxShadow: "0 0 0 1px rgba(0,0,0,.1)",
                    display: "inline-block",
                    cursor: "pointer"
                },
                popover: {
                    position: "absolute",
                    zIndex: "2"
                },
                cover: {
                    position: "fixed",
                    top: "0px",
                    right: "0px",
                    bottom: "0px",
                    left: "0px"
                }
            }
        });

        var onChangeCallback = this.props.onChange;

        return (
            <div>
                {onChangeCallback == null &&
                    <div>
                        Missing 'onChange' props containing callback function.
                </div>
                }
                {onChangeCallback != null &&
                    <div>
                        <div style={styles.swatch} onClick={this.handleClick}>
                            <div style={styles.color} />
                        </div>
                        {this.state.displayColorPicker ? (
                            <div style={styles.popover}>
                                <div style={styles.cover} onClick={this.handleClose} />
                                <SketchPicker
                                    color={this.state.color}
                                    onChange={this.handleChange}
                                />
                            </div>
                        ) : null}
                    </div>
                }
            </div>
        );
    }
}

export default ColorPicker;